import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  FormField, FormInput, FormLabel, FormSubmit, FormWrap,
} from 'src/components/Form/styled';
import { checkAndLoadRecaptcha } from 'src/utils/utils';


class Form extends Component {
  componentDidMount() {
    checkAndLoadRecaptcha(this.props.recaptchaKey);
  }

  getFieldType = (attrType) => {
    switch (attrType) {
      case 'STRING':
        return 'text';
      case 'TEXT':
        return 'textarea';
      default:
        return attrType.toLowerCase();
    }
  };

  render() {
    const { form, onSubmit, id } = this.props;
    return (
      <FormWrap id={id} onSubmit={onSubmit}>
        {form.attributes.map((attr) => {
          const type = this.getFieldType(attr.type);
          const required = attr.required !== undefined ? attr.required : true;

          return (
            <FormField key={attr.name}>
              <FormLabel>
                {attr.label}
                {required && <i>*</i>}
                <FormInput
                  name={attr.name}
                  type={type}
                  as={type === 'textarea' ? 'textarea' : 'input'}
                  required={required}
                />
              </FormLabel>
            </FormField>
          );
        })}
        <FormSubmit>{form.buttonLabel}</FormSubmit>
      </FormWrap>
    );
  }
}

Form.propTypes = {
  form: PropTypes.shape({
    buttonLabel: PropTypes.string.isRequired,
    attributes: PropTypes.array,
  }).isRequired,
  onSubmit: PropTypes.func.isRequired,
  id: PropTypes.string,
  recaptchaKey: PropTypes.string.isRequired,
};

Form.defaultProps = {
  id: '',
};


export default Form;
