import {
  FINISH_LOADING_PAGE,
  SET_CURRENT_PAGE,
  START_LOADING_PAGE,
  STORE_PAGE_CONFIG,
} from 'src/services/pages/types';


const initialState = {
  current: '',
  isLoading: false,
};


const reducer = (state = initialState, action) => {
  switch (action.type) {
    case STORE_PAGE_CONFIG:
      return {
        ...state,
        [action.pageName]: action.data,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        current: action.pageName,
      };
    case START_LOADING_PAGE:
      return {
        ...state,
        isLoading: true,
      };
    case FINISH_LOADING_PAGE:
      return {
        ...state,
        isLoading: false,
      };
    default:
      return state;
  }
};


export default reducer;
