import React from 'react';
import PropTypes from 'prop-types';
import {
  PageBody,
  PageBodyContent,
  PageBodyForm,
  PageContentWrap,
  PageHeader,
  PageHeaderBack,
  PageHeaderTitle,
  PageContentLoader,
  ResponseClose,
  ResponseContent,
  ResponseField,
  ResponseFieldLabel,
  ResponseFieldValue,
  ResponseList, ResponseLists, ResponseMessage,
  ResponseWrap,
} from 'src/components/PageContent/styled';
import { withRouter } from 'react-router-dom';
import { parseMarkdownToHtml } from 'src/utils/utils';
import Buttons from 'src/components/Buttons';
import Form from 'src/components/Form';
import Loader from 'src/components/Loader';
import Map from 'src/components/Map';
import { DEFAULT_ERR_MSG, sendForm } from 'src/services/network';
import { connect } from 'react-redux';
import { mapsKeySelector, recaptchaKeySelector } from 'src/services/options/selectors';


class PageContent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isRequesting: false,
      responseData: null,
      responseError: '',
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.pageName !== this.props.pageName) {
      this.clearResponseState();
    }
  }

  submitForm = (event) => {
    event.preventDefault();

    const { pageConfig } = this.props;
    const { form } = pageConfig;

    this.setState(() => ({ isRequesting: true }));

    this.props.sendForm()
      .then((data) => {
        let res;
        if (data.status === true) {
          res = { successMessage: form.successMessage };
        } else {
          res = data;
        }

        this.setState(() => ({ responseData: res, isRequesting: false }));
      })
      .catch((err = {}) => {
        this.setState(() => ({
          isRequesting: false,
          responseError: err.message || DEFAULT_ERR_MSG,
        }));
      });

    document.documentElement.scrollTop = 0;
    event.target.reset();
  };

  clearResponseState = () => {
    this.setState(() => ({
      responseData: null,
      responseError: '',
      isRequesting: false,
    }));
  };

  goBack = () => {
    this.props.history.goBack();
  };

  renderPageBodyContent = html => (
    <PageBodyContent
      dangerouslySetInnerHTML={{ __html: html }}
    />
  );

  renderResponseList = () => (
    <ResponseWrap>
      <ResponseClose onClick={this.clearResponseState} />
      <ResponseLists>
        {this.state.responseData.map((resItem, i) => (
        // eslint-disable-next-line react/no-array-index-key
          <ResponseList key={i}>
            <ResponseContent>
              {resItem.map(field => (
                <ResponseField key={`${field.name}_${field.value}`}>
                  <ResponseFieldLabel>
                    {field.label}
                    <span>:</span>
                  </ResponseFieldLabel>
                  <ResponseFieldValue value={field.value}>{field.value}</ResponseFieldValue>
                </ResponseField>
              ))}
            </ResponseContent>
          </ResponseList>
        ))}
      </ResponseLists>
    </ResponseWrap>
  );

  renderFormResponse = () => {
    const { responseData } = this.state;

    if (responseData.successMessage) return this.renderFormMessage(responseData.successMessage);
    if (responseData.length) return this.renderResponseList();
    return <div />;
  };

  renderFormMessage = message => (
    <ResponseWrap>
      <ResponseClose onClick={this.clearResponseState} />
      <ResponseMessage error={!!this.state.responseError}>{message}</ResponseMessage>
    </ResponseWrap>
  );

  renderPageBodyForm = form => (
    <PageBodyForm>
      <Form
        id="page-form"
        form={form}
        onSubmit={this.submitForm}
        recaptchaKey={this.props.recaptchaKey}
      />
    </PageBodyForm>
  );

  renderPageLoader = () => (
    <PageContentLoader>
      <Loader />
    </PageContentLoader>
  );

  renderPageBody = () => {
    const { pageConfig, recaptchaKey } = this.props;
    const { responseData, isRequesting, responseError } = this.state;
    const { content, form } = pageConfig;

    return (
      <PageBody>
        {isRequesting && this.renderPageLoader()}
        {responseError && this.renderFormMessage(responseError)}
        {responseData && this.renderFormResponse()}
        {content && this.renderPageBodyContent(parseMarkdownToHtml(content))}
        {(form && recaptchaKey) && this.renderPageBodyForm(form)}
      </PageBody>
    );
  };

  render() {
    const { pageConfig, pageName, mapsKey } = this.props;
    const {
      title, content, form, buttons, map,
    } = pageConfig;

    return (
      <PageContentWrap pageName={pageName}>

        <PageHeader pageName={pageName} id="page-title">
          <PageHeaderBack
            onClick={this.goBack}
            direction="left"
          />
          <PageHeaderTitle>{title}</PageHeaderTitle>
        </PageHeader>

        {!!buttons.length && <Buttons className="hidden-desktop-up" />}
        {(content || form) && this.renderPageBody()}
        {(map && mapsKey) && <Map mapsKey={mapsKey} />}
      </PageContentWrap>
    );
  }
}


PageContent.propTypes = {
  pageConfig: PropTypes.shape({
    title: PropTypes.string.isRequired,
    content: PropTypes.string,
    form: PropTypes.object,
    buttons: PropTypes.array,
    map: PropTypes.bool,
  }),
  history: PropTypes.shape({
    goBack: PropTypes.func.isRequired,
  }).isRequired,
  pageName: PropTypes.string.isRequired,
  sendForm: PropTypes.func.isRequired,
  recaptchaKey: PropTypes.string,
  mapsKey: PropTypes.string,
};

PageContent.defaultProps = {
  pageConfig: {
    buttons: [],
  },
  recaptchaKey: '',
  mapsKey: '',
};

const mapStateToProps = state => ({
  recaptchaKey: recaptchaKeySelector(state),
  mapsKey: mapsKeySelector(state),
});

const mapDispatchToProps = dispatch => ({
  sendForm: () => dispatch(sendForm()),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PageContent));
