import axios from 'axios';
import { formConfigSelector } from 'src/services/pages/selectors';
import { serializeForm } from 'src/utils/utils';
import { recaptchaKeySelector } from 'src/services/options/selectors';


export const DEFAULT_ERR_MSG = 'The request cannot be sent at the moment, please try again later.';


/**
 * @param {String} url
 * @returns {Promise<AxiosResponse<T>>}
 */
const get = (url) => {
  let _url = url;

  if (DEVELOPMENT || IS_DEV_SERVER) _url = _url.replace('v1', 'test');

  return axios.get(_url)
    .then(res => res)
    .catch(() => {
      throw new Error(DEFAULT_ERR_MSG);
    });
};

const post = (url, data) => {
  let _url = url;

  if (DEVELOPMENT || IS_DEV_SERVER) _url = _url.replace('v1', 'test');

  return axios({
    method: 'POST',
    url: _url,
    data,
  })
    .then(res => res)
    .catch(() => {
      throw new Error(DEFAULT_ERR_MSG);
    });
};

const runRecaptcha = key => new Promise((resolve, reject) => {
  // TODO: gain captcha access and remove
  if (DEVELOPMENT) return resolve('1');

  grecaptcha.execute(key)
    .then((token) => {
      resolve(token);
    }, (e) => {
      console.log(e);
      reject();
    });
});

/**
 * Handles form specific errors
 * @param {Object|Array} data - res.data
 * @param {String} data.error - api error message
 * @returns {Promise<{}|{error: String}>}
 */
const processFormResponse = (data) => {
  if (data.error) {
    return Promise.reject(new Error(data.error));
  } if ((Array.isArray(data) && data.length === 0)) {
    return Promise.reject(new Error(DEFAULT_ERR_MSG));
  }

  return Promise.resolve(data);
};

export const sendForm = () => (dispatch, getState) => {
  const state = getState();
  const form = document.getElementById('page-form');
  const formConfig = formConfigSelector(state);
  const recaptchaKey = recaptchaKeySelector(state);
  const { url: servicePath, method } = formConfig;

  let url = `${SERVER_URL}${servicePath}`;
  const data = serializeForm(method, form);

  switch (method) {
    case 'GET': {
      url = `${url}?${data}`;

      return runRecaptcha(recaptchaKey)
        .then((token) => {
          url += `&_recaptcha_token=${encodeURIComponent(token)}`;
        })
        .then(() => get(url))
        .then(res => processFormResponse(res.data));
    }
    case 'POST': {
      data.title = formConfig.title;

      return runRecaptcha(recaptchaKey)
        .then((token) => {
          data._recaptcha_token = token;
        })
        .then(() => post(url, data))
        .then(res => processFormResponse(res.data));
    }
    default:
      console.warn('Unhandled form method');
      return Promise.reject(new Error(DEFAULT_ERR_MSG));
  }
};

const http = {
  get,
};


export default http;
