import {
  FINISH_LOADING_PAGE,
  SET_CURRENT_PAGE,
  START_LOADING_PAGE,
  STORE_PAGE_CONFIG,
} from 'src/services/pages/types';


export const startLoadingPage = () => ({
  type: START_LOADING_PAGE,
});

export const finishLoadingPage = () => ({
  type: FINISH_LOADING_PAGE,
});

export const storePageConfig = (pageName, data = {}) => ({
  type: STORE_PAGE_CONFIG,
  pageName,
  data,
});

export const setCurrentPage = pageName => ({
  type: SET_CURRENT_PAGE,
  pageName,
});
