import { BrowserRouter as Router, Route } from 'react-router-dom';
import Header from 'src/components/Header';
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { fetchInitConfig } from 'src/app/thunks';
import Footer from 'src/components/Footer';
import Services from 'src/components/Services';
import { Tablet } from 'src/components/utils/Media';
import Page from 'src/components/Page';
import { checkNewVersion, mapPathToPageName } from 'src/utils/utils';
import { isPageLoadingSelector } from 'src/services/pages/selectors';
import Loader from 'src/components/Loader';
import { PageLoader } from 'src/components/Page/styled';


class Routes extends React.Component {
  componentDidMount() {
    checkNewVersion();
    this.props.fetchInitConfig();
  }


  render() {
    const { isLoading } = this.props;

    return (
      <Router>
        {isLoading && (<PageLoader><Loader /></PageLoader>)}
        <Header />
        <main>
          <Route
            path="/"
            render={({ location }) => {
              const path = location.pathname;
              const pageName = path === '/' ? 'Main' : mapPathToPageName(path.replace(/\//g, ''));

              return <Page pageName={pageName} />;
            }}
          />
        </main>
        <Tablet>
          <Services />
        </Tablet>
        <Footer />
      </Router>
    );
  }
}

Routes.propTypes = {
  isLoading: PropTypes.bool.isRequired,
};

Routes.propTypes = {
  fetchInitConfig: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  isLoading: isPageLoadingSelector(state),
});

const mapDispatchToProps = dispatch => ({
  fetchInitConfig: () => dispatch(fetchInitConfig()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Routes);
